/*FIRST SCREEN*/


@media only screen and (max-width: 500px) {
  /* Estilos para teléfonos móviles */
  .content-page {
    width: 120vw;
  }
  .content-page__title {
    font-size: 2.1em;
    text-align: center;
  }
  .content-page__text {
    font-size: calc(0.1em + 6vw);
  }
  .content-page__span-msg {
    font-size: calc(0.1em + 5vw);
  }
  .navbar__logo {
    height: 15px;
    margin-top: 25px !important;
  }
  .navbar__back-btn {
    left: 15px;
    top: 0;
    height: 65px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(../experience/img/back-btn-v2.png);
  }
  .action-container__start-btn {
    width: calc(100vw / 1.7);
    height: calc(100vw / 1.7);

  }

  .first-screen
  .first-screen__intro-container {
    width: 90vw;
    padding-bottom: 10vh;
  }
  .load-screen {
    padding: 0;
    width: 100vw;
    height: 100vh;
  }
  .load-screen__span{
    width: 80%;
  }
  .intro-container__title {
    font-size: calc(1.2em + 8vw);
    max-width: 100%;
  }
  .intro-container__sub-title {
    font-size: 1em;
  }
  .intro-container__control-panel{
    padding: 0;
    height: 55px;
    width: 100vw;
  }
  .content-page .control-panel__intro-btn{
    height: fit-content;
    border:  unset;
    padding: 15px 0;
    font-size: calc(0.1em + 6vw) !important;
    width:  calc(100% / 2);
    margin: 10px 5px;
  }
  .content-page .intro-btn__text {
    font-size: inherit;
  }
  .control-panel__intro-btn{
    padding: 10px 0;
    height: fit-content;
    width:  calc(100% / 2.7);
  }

  .intro-btn__text {
    margin: 0;
    font-size: calc(100vw / 25);
  }

  .intro-btn__icon {
    background-size: contain;
    padding: 0;
  }

  .footer__text {
    text-align: center;
    font-size: calc(0.1em + 3vw);
  }
  .footer__action-btn {
    margin: 0 20px;
    height: 40%;
  }
  .footer--sound-btn {
    background-image: url("../experience/img/anim/movil-sound-btn.gif");
  }
  .footer--sound-btn.off{
    background-image: url("../experience/img/anim/movil-sound-btn-off.png");
  }

  .element.active img {
    width: 180px;
  }
  .element.active h2 {
    font-size: 1.9em;
  }
  .element.active span {
    font-size: 1.5em;
  }
  .footer__social{
    width: calc(100vw / 3);
  }
  .credit{
    width: 88vw;

    font-size: calc(0.1em + 3vw);
  }
  .credit__back-btn {
  
    width: 120px;
    height: 10%;
  }
  .credit__item {
    font-size: calc(0.1em + 4vw);

    margin-bottom: 15px;
    text-align: center;
  }
  .credit__more-info {
    width: 90%;
  }
  .credit__footer{
    bottom: calc(18px + 2vh);
  }
}


  /* Estilos para teléfonos móviles */

  @media only screen and (max-width: 375px) {
    .content-page {
      width: 170vw;
    }
    
.btn-container__icon {
  height: 42px;
}
  }